import React, { useState, useEffect, useRef } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import axios from 'axios'
import moment from 'moment'
import Swal from 'sweetalert2'
import { apiUrl } from '../config.json'
import {
  CCreateElement,
  CSidebar,
  CSidebarBrand,
  CSidebarNav,
  CSidebarNavDivider,
  CSidebarNavTitle,
  CSidebarMinimizer,
  CSidebarNavDropdown,
  CSidebarNavItem,
} from '@coreui/react'
import auth from '../components/services/authService'
import kbthlogo from '../assets/icons/dara.png'
import CIcon from '@coreui/icons-react'
import 'src/sidebar.css'
// sidebar nav config
import adminNav from './_nav'
import frontDesk from './frontDesk'
import supervisor from './supervisor'
import loanOfficer from './loanOfficer'
import recovery from './recovery'
const TheSidebar = () => {
  const [user, setUser] = useState({})
  const dispatch = useDispatch()
  const show = useSelector((state) => state.sidebarShow)
  const myUser = auth.getCurrentUser()
  const [userMenu, setUserMenu] = useState([])

  const handleBackup = async () => {
    await axios.post(apiUrl + '/backup', {
      db: 'Daraplus',
    })
  }

  setInterval(function () {
    handleBackup()
    //code goes here that will be run every 5 seconds.
  }, 14400000)

  //console.log(Object.keys(myUser).length)
  //console.log(myUser)
  return (
    <CSidebar
      className="sidebar"
      show={show}
      onShowChange={(val) => dispatch({ type: 'set', sidebarShow: val })}
    >
      <CSidebarBrand className="d-md-down-none" to="/dashboard">
        <h5>Dara Capital Consult</h5>
        {/* <img src={kbthlogo} alt="no img" width={225} height="80" />*/}
        {/*<CIcon
          className="c-sidebar-brand-full"
          name="logo-negative"
          height={35}
        />*/}
        <CIcon
          className="c-sidebar-brand-minimized"
          name="sygnet"
          height={30}
        />
      </CSidebarBrand>
      <CSidebarNav>
        <CCreateElement
          className="items"
          items={
            myUser.userRole == 'Customer Service Officer'
              ? frontDesk
              : myUser.userRole == 'Supervisor'
              ? supervisor
              : myUser.userRole == 'Loan Officer'
              ? loanOfficer
              : myUser.userRole == 'Recovery Officer'
              ? recovery
              : adminNav
          }
          components={{
            CSidebarNavDivider,
            CSidebarNavDropdown,
            CSidebarNavItem,
            CSidebarNavTitle,
          }}
        />
      </CSidebarNav>
      <CSidebarMinimizer className="c-d-md-down-none" />
    </CSidebar>
  )
}

export default React.memo(TheSidebar)
